.recipe_item {
  box-shadow: 0px 5px 20px rgb(79, 72, 72);
  color: rgb(255, 251, 251);
  font-family: poppins;
  width: 550px;
  position: relative;
  margin-top: 40px;
  background: rgb(47, 64, 119);
  padding: 15px 25px 25px 25px;
  border-radius: 10px;

  &--title {
    font-size: 2rem;
    display: inline-block;
    font-weight: bold;
    margin: 0;
    // margin:15px 0;
    margin-bottom: 20px;
  }
  &--image {
    height: 300px;
    width: 300px;
    align-self: center;
  }

  &--content {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
  &--ingredients {
    font-weight: 400;
    font-size: 1.2rem;
    line-height: 25px;
    width: 100%;
    height: 300px;
    overflow-y: scroll;
    &::-webkit-scrollbar {
      display: none;
    }

    ul {
      margin: 0;
      display: flex;
      flex-direction: column;
      gap: 15px;
      // list-style:none;
    }

    .hide {
      display: none;
    }
  }

  &--ingredients-title {
    position: absolute;
    right: 80px;
    top: 30px;
    font-weight: 500;
    font-size: 2rem;
    font-family: Dancing Script;
    text-decoration: underline;
  }
}
