@import "./recipes";
@import url("https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;500;600&family=Edu+NSW+ACT+Foundation:wght@400;500;600&family=Pacifico&family=Poppins:wght@300;400;500;600;700&display=swap");

*{margin:0;}
body {
  .recipe-container {
    display: grid;
    justify-items: center;
    grid-template-columns: repeat(2, 1fr);
    // column-gap: 150px;
    row-gap: 0px;
    width: 100%;
  }
  .container {
    display: flex;
    gap: 50px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom:50px;
  }

  font-family: poppins;
  background: rgb(239, 229, 229);
  form {
    background:rgba(0, 0, 0, 0.85);
    height:120px;
    align-items:center;
    display: flex;
    position: relative;
    width: 100%;
    justify-content: center;

    .logo {
      position: absolute;
      //   font-family:Dancing Script;
      font-family: Pacifico, cursive;
      font-weight: bold;
    //   color:rgb(47, 64, 119);
      color:rgb(255, 255, 255);
      margin: 0;
      justify-self: start;
      font-size: 3.5rem;
      align-self:center;
      padding-bottom:10px;
      left: 80px;
    }
  }
  .form-input {
    padding: 10px;
    min-width: 350px;
    font-weight: 500;
    outline: none;
    // background:rgb(255, 255, 255);
    height:30px;
    border: none;
    font-size: 1.5rem;
  }
  button {
    height:50px;
    // padding: 10px;
    align-self:center;
    cursor: pointer;
    width: 100px;
    color: white;
    border: none;
    background: rgb(47, 64, 119);
    font-size: 1.2rem;
  }

}
